.button {
    width: 300px;
    height: 65px;
    border-radius: 30px;
    border: none;
    font-size: 18px;
    font-weight: 700;
    background-color: #00C78B;
    color: #FFFFFF;
}

.wrapper {
    position: relative;
}

.list {
    position: absolute;
    width: 300px;
    padding: 20px 32px;
    font-weight: 700;
    font-size: 18px;
    background-color: #00C78B;
    color: #FFFFFF;
    border-radius: 30px;
    top: 68px;
    left: 0;
}

.item {
    cursor: pointer;
    line-height: 36px;
    margin-bottom: 10px;
}

.item:last-child {
    margin-bottom: 0;
}

.item:hover {
    background-color: #00d096;
    transition: background-color .3s ease-in;
}

@media (max-width: 1024px) {
    .buttons {
        gap: 20px;
    }

    .button {
        width: 220px;
        height: 45px;
        font-size: 14px;
    }
}
@media (max-width: 480px) {
    .buttons {
        flex-direction: column;    }
}
